<template>
	<view class="w-100">
		<view class="w-100 main-bg-color border-bottom" style="height: 60rpx;">
			<view class="font-36 font-weight text-center">蓝牙设备</view>
		</view>
		
		<view class="d-flex flex-row a-center mt-3">
			<view class="span24-12 d-flex a-center j-center">
				<view class="border btn-orange-white font-36 p pl-2 pr-2 text-center" @click="startBluetoothDeviceDiscovery">搜索蓝牙</view>
			</view>
			<view class="span24-12 d-flex a-center j-center">
				<view class="border btn-orange-white font-36 p pl-2 pr-2 text-center" @click="stopBluetoothDevicesDiscovery">停止搜索</view>
			</view>
		</view>
		
<!-- 		<view class="d-flex mt-4 a-center j-center" v-if="getConnectInfo">
			<view class="border p-1 bg-light-secondary rounded-10 position-relative" style="width: 94%;">
				<view class="font-30">最后连接设备: {{getConnectInfo}}</view>
				<view class="text-center pl-1 pr-1 border btn-blue-white" style="right: 5rpx;" @click="connectBlue">重连</view>
			</view>
		</view> -->

		
		<scroll-view scroll-y :style="'height:'+(totalH - 80)+'px;'">
			<view class="d-flex flex-column mt-1 j-center a-center">
				<block v-for="(item, index) in devicesList" :key="index">
					<view class="border p mt-1 bg-light-secondary rounded-10" style="width: 94%;" v-if="item.name != ''"
					@click="connectBlue(item)">
						<view class="font-30">名称: {{item.name}}</view>
						<view style="font-30">信号强度: {{item.RSSI}}dBm ({{Math.max(100+item.RSSI,0)}}%)</view>
						<view class="font-30">{{item.deviceId}}</view>
					</view>
				</block>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
	export default {
		components:{
		},
		data() {
			return {
				devicesList:[]
			}
		},
		onLoad() {
			uni.getSystemInfo({
				success: (res) => {
					this.totalH = res.windowHeight - uni.upx2px(62)
				}
			})
			
			uni.openBluetoothAdapter({
				success(res) {
					console.log('初始化蓝牙模块成功')
				}
			})
		},
		computed:{
			...mapState({
			}),
			...mapGetters([
			]),
		},
		methods: {
			...mapMutations([
			]),
			...mapActions([
			]),
			
			startBluetoothDeviceDiscovery(){
				console.log('startBluetoothDeviceDiscovery')
				var _self = this
				uni.showLoading({title: '蓝牙搜索中'})
				setTimeout(() => {
					uni.startBluetoothDevicesDiscovery({
						success: res => {
							console.log(res)
							uni.onBluetoothDeviceFound(devices => {
								//不重复,就添加到devicesList中,
								if (!_self.devicesList.some(item => {
										return item.deviceId == devices.devices[0].deviceId
									})) {
									console.log(devices)
									_self.devicesList.push(devices.devices[0])
								}
							});
						},
						fail: err => {
							console.log('搜索设备失败' + JSON.stringify(err))
						}
					})
				}, 200)
			},
			
			stopBluetoothDevicesDiscovery(){
				this.$Blue.stopBluetoothDevicesDiscovery()
			},
			connectBlue(item){
				this.$Blue.connectBlueTooth(item)
			},
			writeBLECharacteristicValue() {
				this.$Blue.writeBLECharacteristicValue()
			},
		}
	}
</script>

<style>

